import Bus from "../bus.js"
export default {
	name: "pick",
	data() {
		
		const currentDate = this.getDate({
			format: true
		})
		return {
			title: 'picker',
			date: currentDate,
			time: '12:01',
			width:0,
			index:0,
			
		}
	},
	props:{
		type:{
			type:String,
			default:'picker'
		},
		array:{
			type:Array,
			default:[0,0]
		},
		num:{
			type:Array,
			default:[0,0]
		},
		disabled:{
			type:Boolean,
			default:false
		},
		size:{
			type:Number,
			default:14
		}
	},
	created() {
		const res = uni.getSystemInfoSync();
		console.log(res.windowHeight+' '+res.windowWidth);
		this.width=res.windowWidth*0.6;
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	methods: {
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value)
			this.index = e.target.value;
			Bus.$emit('change', [this.array[this.index], this.num[1]]);
		},
		bindDateChange: function(e) {
			this.date = e.target.value
		},
		bindTimeChange: function(e) {
			this.time = e.target.value
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	},
	watch:{
		
		num: {
			handler(old,newval) {
				console.log(old+'='+newval);
				if(old[0]!=undefined){
					
					this.index=old[0];
					// Bus.$emit('change', [this.array[this.index], old[1]]);
				}else{
					console.log(423);
					// Bus.$emit('change', [this.array[0], this.num[1]]);
				}
				
			},
			immediate: true
		},
		array: {
			handler(old,newval) {
				// console.log(old[this.index]+'='+newval+'='+this.array[this.index]+'='+this.index+'='+this.num);
				// console.log(old[this.index]+'?'+this.num[1]);
				var _this=this;
				setTimeout(function() {
					Bus.$emit('change', [old[_this.index], _this.num[1]]);
				}, 500);
			},
			immediate: true
		}
	}
}
